body {
  margin: 0;
}

.blocks {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: stretch;
}

.block {
  height: 300px;
  min-width: 300px;
  font-family: Arial, Helvetica, sans-serif, "Times New Roman", Times, serif;
  font-size: 100px;
  color: white;
  text-align: center;
  line-height: 300px;
  flex: 1;
}

.block--1 {
  background-color: rgb(255, 0, 0);
}

.block--2 {
  background-color: rgb(204, 0, 0);
}

.block--3 {
  background-color: rgb(153, 0, 0);
}

.block--4 {
  background-color: rgb(102, 0, 0);
}

.block--5 {
  background-color: rgb(51, 0, 0);
}

.block--6 {
  background-color: rgb(0, 0, 0);
}

@media (min-width: 600px) {
  .block {
    max-width: 50%;
  }
  .block--3 {
    order: 2;
  }
  .block--4 {
    order: 1;
  }
  .block--5 {
    order: 3;
  }
  .block--6 {
    order: 4;
  }
}

@media (min-width: 900px) {
  .block {
    min-width: 33.33%;
  }
  .block--3 {
    order: 0;
  }
  .block--4 {
    order: 3;
  }
  .block--5 {
    order: 2;
  }
  .block--6 {
    order: 1;
  }
}
